import React from "react";
import styles from "./Navbar.module.scss";
import Hamburger from "../Hamburger/Hamburger";
import { imageObj } from "../../imageImports";
const Logo = imageObj.socials[4];
const Navbar = (props) => {
  return (
    <nav className={styles["wrapper"]}>
      <div className={styles["container"]}>
        <div className={styles["left-container"]}>
          <Hamburger {...props} />
          <label className={styles["nav-menu-label"]} htmlFor="burger-trigger">
            <p className={styles["secondary-main-trigger-icon"]}>menu</p>
          </label>
        </div>

        <div className={styles["logo-container"]}>
          <a href="https://www.apeldesign.com/">
            <Logo  fill="white" className={styles["logo"]}></Logo>
          </a>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
