import clsx from "clsx";
import React from "react";
import { imageObj } from "../../imageImports";
import styles from "./ModalGallery.module.scss";

const ModalGallery = (props) => {
  return (
    <div
      id="behind the scenes"
      className={props.hidden ? "inactive" : styles["container"]}
    >
      <div className="title has-text-centered">behind the scenes</div>
      <div className={styles["gallery-container"]}>
        {imageObj.resizedBts.map((img, i) => {
          return (
            <img
              key={i}
              onClick={() => {
                props.handleShowModal(i, "image");
              }}
              className={clsx({
                [`${styles["image-animation"]}`]: props.showModal,
              })}
              src={img}
              alt=""
            />
          );
        })}
      </div>
    </div>
  );
};

export default ModalGallery;
