import React, { useEffect, useState } from "react";
import { imageObj } from "../../imageImports";
import styles from "./Gallery.module.scss";

import classnames from "classnames";
import clsx from "clsx";
const random = [];
for (let i = 0; i < 15; i++) {
  random.push(Math.random());
}

const Image = (props) => {
  return (
    <div
      style={{
        border: props.selectedGallery === props.index ? "2px solid white" : "",
      }}
      className={
        styles[`grid-image-container`] +
        " " +
        clsx({
          [styles["overlay-image"]]: props.selectedGallery === props.index,
        })
      }
    >
      <img
        onClick={() => props.handleSelect(props.index)}
        src={imageObj.resizedBts[props.index]}
        alt=""
      />
    </div>
  );
};
const Gallery = (props) => {
  const [selected, setSelected] = useState(0);
  const handleSelect = (index) => {
    props.setSelectedGallery(index);
  };
  useEffect(() => {
    props.setSelectedGallery(0);
  }, []);
  return (
    <div className={styles[`container`]}>
      <div className={styles[`grid-container`]}>
        {imageObj.bts.map((img, i) => {
          return (
            <Image
              selectedGallery={props.selectedGallery}
              index={i}
              handleSelect={handleSelect}
              key={random[i]}
            ></Image>
          );
        })}
        <iframe
          onClick={() => handleSelect("video")}
          className={styles[`video-modal`]}
          width="100%"
          src="https://www.youtube.com/embed/nTS10ZQM5Ms"
          title="YouTube video player"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        ></iframe>
      </div>
    </div>
  );
};

export default Gallery;
