export const imageImports = {
  thumbnails: {},
  misc: {},
  bgPhotos: {},
  highResGallery: {},
  socials: {},
  renders: {},
  rendersThumbs: {},
  rendersText: {},
  floorPlans: {},
  bts: {},
  rendersFull: {},
  resizedBts: {},
};
const keys = Object.keys(imageImports);
function importAll() {
  for (let i = 0; i < arguments.length; i++) {
    arguments[i]
      .keys()
      .forEach((key) => (imageImports[keys[i]][key] = arguments[i](key)));
  }
}
// Note from the docs -> Warning: The arguments passed to require.context must be literals!
importAll(
  require.context("./images/gallery/thumbs", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/misc", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/bgPhotos", false, /\.(png|jpe?g|svg)$/),
  require.context(
    "./images/propertyImagesHighRes",
    false,
    /\.(png|jpe?g|svg)$/
  ),
  require.context("./images/socials/assets", false, /\.(png|jpe?g|svg)$/),

  require.context("./images/renders", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/rendersThumbs", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/rendersText", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/floorPlans", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/bts", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/rendersFull", false, /\.(png|jpe?g|svg)$/),
  require.context("./images/resizedBts", false, /\.(png|jpe?g|svg)$/)
);
export const imageObj = {};
for (let folder in imageImports) {
  if (!imageObj[folder]) {
    imageObj[folder] = [];
  }
  let i = 0;
  for (let nestFolder in imageImports[folder]) {
    if (folder === "renders" || folder === "rendersThumbs") {
      const y = Object.keys(imageImports[folder]);

      y.sort((a, b) => {
        const numA = a.replace(/.\/|[A-Z|.]|\s/gi, "");
        const numB = b.replace(/.\/|[A-Z|.]|\s/gi, "");
        return parseInt(numA) - parseInt(numB);
      });

      imageObj[folder].push(imageImports[folder][y[i]].default);
      i++;
    } else {
      imageObj[folder].push(imageImports[folder][nestFolder].default);
    }
  }
}
