import clsx from "clsx";
import React from "react";
import { imageObj } from "../../imageImports";
import styles from "./Modal.module.scss";

const Modal = (props) => {
  return (
    <div
      className={clsx({
        modal: true,
        "is-active": props.showModal.visibility,
      })}
    >
      <div
        onClick={() => {
          props.handleShowModal("image");
        }}
        className="modal-background"
      ></div>
      <div className="modal-content">
        <p className="image is-4by3">
          {props.showModal.type === "image" ? (
            <img src={props.showModal.content} alt="" />
          ) : (
            <div className={styles[`video-container`]}>
              <iframe
                height="100%"
                className={styles[`video-modal`]}
                width="100%"
                src="https://www.youtube.com/embed/nTS10ZQM5Ms"
                title="YouTube video player"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>
          )}
        </p>
      </div>
      <button
        onClick={() => {
          props.handleShowModal("image");
        }}
        className="modal-close is-large"
        aria-label="close"
      ></button>
    </div>
  );
};

export default Modal;
