import React from "react";
import styles from "./ImageContent.module.scss";
import ReactPlaceholder from "react-placeholder";
import "react-placeholder/lib/reactPlaceholder.css";
import { imageObj } from "../../imageImports";
import { Carousel } from "react-responsive-carousel";
import clsx from "clsx";
import LazyLoad from "react-lazy-load";
import left from "./images/Behind the Scenes Left.png";
import right from "./images/Behind the Scenes Right.png";
import classnames from "classnames";
const ImageContent = (props) => {
  console.log(imageObj.bgPhotos);
  if (props.type === "carousel") {
    return (
      <div
        className={
          props.hidden
            ? "inactive"
            : props.index === 3
            ? styles[`container`] + " " + styles["blur"]
            : styles["container"]
        }
      >
        <Carousel
          renderArrowPrev={(onClickHandler) => {
            return (
              <button
                onClick={onClickHandler}
                className={styles["control-arrow"] + " " + styles["left"]}
                type="button"
              ></button>
            );
          }}
          renderArrowNext={(onClickHandler) => {
            return (
              <button
                onClick={onClickHandler}
                className={styles["control-arrow"] + " " + styles["right"]}
                type="button"
              ></button>
            );
          }}
          autoPlay={false}
          className={styles[`carousel-container`]}
        >
          {imageObj.floorPlans.map((img) => {
            return (
              <div className={styles[`carousel-image-container`]}>
                <LazyLoad
                  overflow={true}
                  offset={100}
                  once={true}
                  height="100%"
                >
                  <img style={{ height: "100%" }} src={img} alt="" />
                </LazyLoad>
                ;
              </div>
            );
          })}
        </Carousel>
      </div>
    );
  }
  return (
    <div
      className={
        props.hidden
          ? "inactive"
          : props.index === 3 || props.index === 4
          ? styles[`container`] + " " + styles["blur"]
          : styles["container"]
      }
    >
      <div
        onClick={() => {
          if (props.index === 3) {
            props.handleShowModal("video");
          } else {
            return;
          }
        }}
      >
        {props.index === 3 || props.index === 4 ? (
          <img src={imageObj.misc[5]} className={styles[`play-button`]}></img>
        ) : null}
        <div className={styles[`image-container`]}>
          <LazyLoad height="100%">
            <img
              src={imageObj.bgPhotos[props.index]}
              alt=""
              className={styles[`main-image`]}
            />
          </LazyLoad>
        </div>
      </div>
    </div>
  );
};

export default ImageContent;
