import React from "react";
import { imageObj } from "../../imageImports";
import CarouselPage from "../CarouselPage/CarouselPage";
import Credits from "../Credits/Credits";
import ImageContent from "../ImageContent/ImageContent";
import TextContent from "../TextContent/TextContent";
import styles from "./RightContainer.module.scss";
import Gallery from "../Gallery/Gallery";
export default React.forwardRef(function RightContainer(props, ref) {
  {
    return (
      <div ref={ref} className={styles["container"]}>
        <Credits></Credits>

        <ImageContent
          index={4}
          handleShowModal={props.handleShowModal}
        ></ImageContent>

        <Gallery
          setSelectedGallery={props.setSelectedGallery}
          selectedGallery={props.selectedGallery}
          page="right"
        ></Gallery>
        <ImageContent
          handleShowModal={props.handleShowModal}
          index={3}
        ></ImageContent>
        <TextContent index={4}></TextContent>
        <ImageContent type="carousel"></ImageContent>
        <CarouselPage
          carouselPage={props.carouselPage}
          handleRenderCarousel={props.handleRenderCarousel}
          page="right"
        ></CarouselPage>

        <ImageContent index={1}></ImageContent>
        <TextContent index={0}></TextContent>
        <div className={styles[`logo-wrapper`]}>
          <div className={styles[`logo-container`]}>
            <img
              className={styles[`image-logo`]}
              src={imageObj.misc[1]}
              alt=""
            />
          </div>
        </div>
      </div>
    );
  }
});
