import React from "react";
import styles from "./Credits.module.scss";
import left from "./images/Development Credits Left.png";
import right from "./images/Development Credits Right.png";
import { creditsContent } from "../../pageContent";
const Credits = (props) => {
  if (props.type === "mobile") {
    return (
      <div id="credits" className={styles[`mobile-container`]}>
        <div className={styles[`mobile-header-container`]}>
          <h5>development credits</h5>
        </div>
        {creditsContent.map((content) => {
          return (
            <div className={styles[`mobile-credits-container`]}>
              <h5 className={styles[`header`]}>{content.header}</h5>
              {content.paragraphs.map((p) => {
                return <p>{p}</p>;
              })}
            </div>
          );
        })}
      </div>
    );
  }
  return (
    <div
      style={{
        justifyContent: props.page === "left" ? "flex-end" : "flex-start",
      }}
      className={styles[`container`]}
    >
      <div className={styles[`sub-container`]}>
        <div
          style={{
            justifyContent: props.page === "left" ? "flex-end" : "flex-start",
          }}
          className={styles[`title-container`]}
        >
          {props.type !== "mobile" ? (
            <div className={styles[`title-image-container`]}>
              {props.page === "left" ? (
                <img src={left} alt="" />
              ) : (
                <img src={right} alt="" />
              )}
            </div>
          ) : (
            <h5>Development</h5>
          )}
        </div>
        <div className={styles[`content-container`]}>
          {props.page === "left" ? (
            <div className={styles[`credits-container-sub`]}>
              {creditsContent.slice(0, 2).map((content) => {
                return (
                  <div className={styles[`credits-container`]}>
                    <h5 className={styles[`header`]}>{content.header}</h5>
                    {content.paragraphs.map((p) => {
                      return <p>{p}</p>;
                    })}
                  </div>
                );
              })}
            </div>
          ) : (
            <div className={styles[`credits-container-sub`]}>
              {creditsContent.slice(2,6).map((content) => {
                return (
                  <div className={styles[`credits-container`]}>
                    <h5 className={styles[`header`]}>{content.header}</h5>
                    {content.paragraphs.map((p) => {
                      return <p>{p}</p>;
                    })}
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Credits;
