import React, { useEffect, useRef, useState } from "react";
import LeftContainer from "./components/LeftContainer/LeftContainer";
import Modal from "./components/Modal/Modal";
import Navbar from "./components/navbar/Navbar";
import RightContainer from "./components/RightContainer/RightContainer";
import Socials from "./components/Socials/Socials";
import { pageLength } from "./pageContent";
import LazyLoad from "./components/LazyLoad/LazyLoad";
import ScrollWrapper from "./components/ScrollWrapper/ScrollWrapper";
import Credits from "./components/Credits/Credits";
const ScrollContainer = React.createContext("light");

const Index = () => {
  const [windowThreshHold, setWindowThreshHold] = useState(false);
  const [renderCarousel, setRenderCarousel] = useState(0);
  const [page, setCurrentPage] = useState(0);

  const [shouldScroll, setShouldScroll] = useState(true);
  const [pagePositions, setDefaultPositions] = useState({
    left: 0,
    right: 0,
  });
  const leftPage = useRef(null);
  const [showModal, setShowModal] = useState({
    visibility: false,
    content: null,
    type: null,
  });
  const rightPage = useRef(null);
  const handleRenderCarousel = (i) => {
    if (renderCarousel === 0 && i < 0) {
      return;
    }
    if (renderCarousel === 13 && i > 0) {
      return;
    }
    setRenderCarousel(renderCarousel + i);
  };

  useEffect(() => {
    window.onresize = () => {
      if (window.innerWidth <= 650) {
        setWindowThreshHold(true);
        return;
      }
      setDefaultPositions((prev) => {
        const copy = { ...prev };

        // copy.right += difference;

        leftPage.current.style.transform = `translateY( ${
          window.innerHeight * -page
        }px)`;
        rightPage.current.style.transform = `translateY( ${
          window.innerHeight * -(9 - page)
        }px)`;
        copy.left = window.innerHeight * -page;
        copy.right = window.innerHeight * -(pageLength - 1 - page);

        return copy;
      });
    };
  }, [page]);
  useEffect(() => {
    const copy = { ...pagePositions };
    copy.right = -window.innerHeight * 9;

    setDefaultPositions(copy);
  }, []);
  const handleScrollAnimation = (e) => {
    const leftElement = leftPage.current;
    const rightElement = rightPage.current;
    if (window.innerWidth <= 650) {
      return;
    }
    if (shouldScroll) {
      setDefaultPositions((prev) => {
        if (e.deltaY > 0 && page < pageLength - 1) {
          prev.left = prev.left - window.innerHeight;
          prev.right = window.innerHeight + prev.right;
          leftElement.style.transform = `translateY( ${prev.left}px)`;

          rightElement.style.transform = `translateY( ${prev.right}px)`;
          setCurrentPage(page + 1);
        } else if (e.deltaY < 0 && page > 0) {
          prev.left = prev.left + window.innerHeight;
          prev.right = prev.right - window.innerHeight;
          leftElement.style.transform = `translateY( ${prev.left}px)`;

          rightElement.style.transform = `translateY( ${prev.right}px)`;
          setCurrentPage(page - 1);
        }
        setShouldScroll(false);
        setTimeout(() => {
          setShouldScroll(true);
        }, 1100);
        return prev;
      });
    }
  };

  const handleShowModal = (ele, type) => {
    setShowModal({
      visibility: !showModal.visibility,
      content: ele,
      type: type,
    });
  };
  const scrollClick = (pos) => {
    setShouldScroll(false);
    setCurrentPage(pos.index);
    setDefaultPositions({
      left: -pos.left,
      right: pos.right,
    });
    setTimeout(() => {
      setShouldScroll(true);
    }, 1100);
  };

  return (
    <>
      <LazyLoad></LazyLoad>
      <Modal showModal={showModal} handleShowModal={handleShowModal}></Modal>
      <Navbar
        scrollClick={scrollClick}
        leftPage={leftPage}
        setDefaultPositions={setDefaultPositions}
        rightPage={rightPage}
      />
      <div onWheel={handleScrollAnimation} className="scroll-container">
        <ScrollWrapper
          leftPage={leftPage}
          carouselPage={renderCarousel}
          handleRenderCarousel={handleRenderCarousel}
          handleShowModal={handleShowModal}
          rightPage={rightPage}
          windowThreshHold={windowThreshHold}
          renderCarousel={renderCarousel}
        ></ScrollWrapper>
      </div>

      <Socials></Socials>
    </>
  );
};

export default Index;
