import React, { forwardRef } from "react";
import { imageObj } from "../../imageImports";
import { navButtons } from "../../pageContent";
import Card from "../Card/Card";
import CarouselPage from "../CarouselPage/CarouselPage";
import Credits from "../Credits/Credits";
import Gallery from "../Gallery/Gallery";
import ImageContent from "../ImageContent/ImageContent";
import ModalGallery from "../ModalGallery/ModalGallery";
import TextContent from "../TextContent/TextContent";
import styles from "./LeftContainer.module.scss";
import GalleryPreview from "../GalleryPreview/GalleryPreview";
export default forwardRef(function LeftContainer(props, ref) {
  return (
    <>
      <div ref={ref} className={styles["container"]}>
        <div id="home" className={styles[`logo-wrapper`]}>
          <div className={styles[`logo-container`]}>
            <img
              className={styles[`image-logo`]}
              src={imageObj.misc[0]}
              alt=""
            />
            <img
              className={`inactive` + " " + styles["image-logo"]}
              src={imageObj.misc[4]}
              alt=""
            />
          </div>
        </div>

        <div className={styles[`card-container`]}>
          {props.windowThreshHold ? (
            <>
              {Array.from({ length: navButtons.length - 2 }).map((ele, i) => {
                return (
                  <>
                    <Card
                      key={i}
                      handleShowModal={props.handleShowModal}
                      index={i}
                    ></Card>
                  </>
                );
              })}
              <Credits type="mobile"></Credits>
            </>
          ) : null}
        </div>

        <div
          id="main-left-container"
          className={styles["left-container-desktop"]}
        >
          <ImageContent index={0}></ImageContent>
          <TextContent index={0} hidden={true}></TextContent>
          <TextContent
            name="discover"
            bgColor="#a4632e"
            index={1}
          ></TextContent>
          <CarouselPage
            carouselPage={props.carouselPage}
            handleRenderCarousel={props.handleRenderCarousel}
            page="left"
          ></CarouselPage>
          <TextContent index={3}></TextContent>

          <ImageContent index={1} hidden={true}></ImageContent>
          <ImageContent index={2}></ImageContent>
          <TextContent index={2} hidden={true}></TextContent>
          <TextContent bgColor="#a4632e" index={5}></TextContent>
          <GalleryPreview
            selectedGallery={props.selectedGallery}
          ></GalleryPreview>

          <TextContent index={7}></TextContent>
          <Credits page="left"></Credits>
        </div>
      </div>
    </>
  );
});
