import React from "react";
import { textPages } from "../../pageContent";
import styles from "./TextContent.module.scss";
const TextContent = (props) => {
  return (
    <div
      style={{ background: props.bgColor }}
      className={styles[`container`] + (props.hidden ? " " + "inactive" : "")}
    >
      <div className={styles["text-content"]}>
        <div className="content">
          <div className="content is-large">
            <h3 className={styles[`content-header`]}>
              {" "}
              {textPages[props.index].header}
            </h3>
          </div>
          {textPages[props.index].paragraphs.map((text, i) => {
            return (
              <p key={i} className={styles[`content-text`]}>
                {text}
              </p>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default TextContent;
