import React, { useState } from "react";
import LeftContainer from "../LeftContainer/LeftContainer";
import RightContainer from "../RightContainer/RightContainer";
import styles from "./ScrollWrapper.module.scss";

const ScrollWrapper = (props) => {
  const [selectedGallery, setSelectedGallery] = useState(null);



  return (
    <div className={styles[`scroll-container`]}>
      <LeftContainer
        selectedGallery={selectedGallery}
        windowThreshHold={props.windowThreshHold}
        carouselPage={props.renderCarousel}
        handleRenderCarousel={props.handleRenderCarousel}
        handleShowModal={props.handleShowModal}
        ref={props.leftPage}
      />

      <RightContainer
        selectedGallery={selectedGallery}
        setSelectedGallery={setSelectedGallery}
        carouselPage={props.renderCarousel}
        handleRenderCarousel={props.handleRenderCarousel}
        handleShowModal={props.handleShowModal}
        ref={props.rightPage}
      />
    </div>
  );
};

export default ScrollWrapper;
