import React, { useEffect, useRef } from "react";
import styles from "./CarouselPage.module.scss";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";
import { imageImports, imageObj } from "../../imageImports";
import clsx from "clsx";
import LazyLoad from "react-lazy-load";
import CarouselThumbs from "../CarouselThumbs/CarouselThumbs";
import "./customCarousel.scss";
const CarouselPage = (props) => {
  const renders = useRef([]);
  useEffect(() => {
    if (props.page === "left") {
      for (let i = 0; i < 17; i++) {
        renders.current.push(imageObj.renders[i]);
      }
    } else {
      for (let i = 17; i < 31; i++) {
        renders.current.push(imageObj.renders[i]);
      }
    }
  }, []);
  if (props.type === "card") {
    return (
      <Carousel
        height="100%"
        showThumbs={false}
        showStatus={false}
        showIndicators={false}
        className={styles[`carousel-card`]}
      >
        {(props.name === "renders"
          ? imageObj.rendersFull
          : imageObj.floorPlans
        ).map((img) => {
          return <img className={styles[`card-carousel-image`]} src={img} />;
        })}
      </Carousel>
    );
  }
  return (
    <div className={styles[`container`]}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: props.page === "left" ? "flex-end" : "flex-start",
        }}
      >
        <div className="content">
          <img
            src={imageObj.rendersText[props.page === "left" ? 0 : 1]}
            alt=""
          />
        </div>
        <div
          className={styles[`carousel-container`]}
          style={{
            display: "flex",
          }}
        >
          <Carousel
            showThumbs={false}
            showStatus={false}
            showIndicators={false}
            selectedItem={props.carouselPage}
            renderArrowNext={(onClickHandler) => {
              if (props.page === "left") {
                return null;
              } else {
                return (
                  <button
                    className={clsx({
                      [styles["caro-button"]]: true,
                      [styles["right"]]: true,
                    })}
                    onClick={() => {
                      props.handleRenderCarousel(1);
                    }}
                    type="button"
                  >
                    <img src={imageObj.misc[6]} alt="" />
                  </button>
                );
              }
            }}
            renderArrowPrev={(onClickHandler) => {
              if (props.page === "right") {
                return null;
              } else {
                return (
                  <button
                    className={clsx({
                      [styles["caro-button"]]: true,
                      [styles["left"]]: true,
                    })}
                    onClick={() => {
                      props.handleRenderCarousel(-1);
                    }}
                    type="button"
                  >
                    <img src={imageObj.misc[6]} alt="" />
                  </button>
                );
              }
            }}
            className={styles[`carousel`]}
          >
            {renders.current.map((img) => {
              return (
                <div>
                  <LazyLoad>
                    <img src={img} />
                  </LazyLoad>
                </div>
              );
            })}
          </Carousel>
          <CarouselThumbs
            carouselPage={props.carouselPage}
            page={props.page}
          ></CarouselThumbs>
        </div>
      </div>
    </div>
  );
};

export default CarouselPage;
