import React, { useEffect, useRef, useState } from "react";
import { imageObj } from "../../imageImports";
import styles from "./CarouselThumbs.module.scss";
import LazyLoad from "react-lazy-load";
const CarouselThumbs = (props) => {
  const initalIndex = useRef(0);
  const oldPage = useRef(0);
  const [renders, setRenders] = useState([]);

  useEffect(() => {
    const copy = [];
    if (props.carouselPage < oldPage.current) {
      
      initalIndex.current -= 15;
    }
    if (props.page === "left") {
      for (let i = initalIndex.current; i < initalIndex.current + 5; i++) {
        copy.push(imageObj.rendersThumbs[i]);
      }
    } else {
      for (let i = initalIndex.current + 5; i < initalIndex.current + 10; i++) {
        copy.push(imageObj.rendersThumbs[i]);
      }
    }

    initalIndex.current += 10;

    setRenders(copy);
  }, [Math.floor(props.carouselPage / 10)]);
  useEffect(() => {
    oldPage.current = props.carouselPage;
  }, [props.carouselPage]);
  return (
    <div
      style={{
        padding:
          props.page === "left" ? "25px 7.5px 0px 0px" : "25px 0px 0px 7.5px",
        justifyContent: props.page === "left" ? "flex-end" : "flex-start",
      }}
      className={styles[`container`]}
    >
      {renders.map((img, i) => {
        return img ? (
          <div
            style={{
              border:
                props.carouselPage === (props.page === "right" ? i + 5 : i) ||
                props.carouselPage - 10 === (props.page === "right" ? i + 5 : i)
                  ? "white 2px solid"
                  : "",
            }}
            className={styles[`image-container`]}
          >
            <img src={img} alt="" />;
          </div>
        ) : (
          <div
            style={{
              backgroundColor: "black",
            }}
            className={styles[`image-container`]}
          ></div>
        );
      })}
    </div>
  );
};

export default CarouselThumbs;
