import React from "react";
import { imageObj } from "../../imageImports";
import styles from "./Socials.module.scss";
const socials = ["Facebook", "Twitter", "Instagram", "Linkedin"];
const svgLogos = imageObj.socials.filter((svg) => {
  return socials.includes(svg.name);
});
const Socials = () => {
  return (
    <div>
      <div className={styles["container"]}>
        {svgLogos.map((Svg, i) => {
          return <Svg key={i}></Svg>;
        })}
      </div>
    </div>
  );
};

export default Socials;
