import clsx from "clsx";
import React, { useState } from "react";
import { imageObj } from "../../imageImports";
import { navButtons, pageContent, textPages } from "../../pageContent";
import CarouselPage from "../CarouselPage/CarouselPage";
import ModalGallery from "../ModalGallery/ModalGallery";
import styles from "./Card.module.scss";

const Arrow = imageObj.socials[0];

const cardLayout = [
  "malibu life",
  "discover",
  "renders",
  "floorplans",

  "equestrian",
  "video",
  "pictures",
  "drone footage",
  "credits",
];
const cardImageLayout = [
  imageObj.bgPhotos[0],
  imageObj.bgPhotos[1],
  null,
  null,
  imageObj.bgPhotos[2],
  imageObj.bgPhotos[3],
  "",
  imageObj.bgPhotos[4],

];

const Card = (props) => {
  const [showMore, setShowMore] = useState(false);
  if (props.index === 6) {
    return <ModalGallery></ModalGallery>;
  }
  return (
    <div
      id={cardLayout[props.index]}
      className={clsx({
        [styles["card-container"]]: true,
        inactive: true,
        card: true,
      })}
    >
      <div
        onClick={() => {
          if (
            cardLayout[props.index] === "video" ||
            cardLayout[props.index] === "drone footage"
          ) {
            props.handleShowModal(null, "video");
          } else {
            return;
          }
        }}
        className="card-image"
      >
        <figure
          onClick={props.handleGalleryModal}
          className={clsx({
            "image is-4by3": true,

            [styles["blur"]]:
              cardLayout[props.index] === "video" ||
              cardLayout[props.index] === "drone footage",
          })}
        >
          {cardLayout[props.index] === "video" ||
          cardLayout[props.index] === "drone footage" ? (
            <div className={styles[`play-button-container`]}>
              <figure className={`image is-square `}>
                <img src={imageObj.misc[5]}></img>
              </figure>
            </div>
          ) : null}

          {cardImageLayout[props.index] === null ? (
            <div className={styles[`carousel-container`]}>
              <CarouselPage
                name={cardLayout[props.index]}
                type="card"
              ></CarouselPage>
            </div>
          ) : (
            <img src={cardImageLayout[props.index]} />
          )}
        </figure>
      </div>
      <div
        className={clsx({
          "card-content": true,
          [styles["card-content"]]: true,
        })}
      >
        <div className="media">
          <div className="media-left">
            <figure className="image is-48x48">
              <div style={{ background: "white", height: "100%" }}></div>
            </figure>
          </div>

          <p className="title is-4">
            {textPages[props.index] ? textPages[props.index].header : null}
          </p>
        </div>
        <div
          style={{
            display: showMore ? "block" : "",
            maxHeight: showMore ? "100%" : "",
          }}
          className={clsx({
            "content is-clipped": true,

            [styles["content"]]: true,
          })}
        >
          {textPages[props.index]
            ? textPages[props.index].paragraphs.map((p, i) => {
                return <p key={i}>{p}</p>;
              })
            : null}
        </div>{" "}
        <br />
        <div className="level is-mobile">
          <div
            onClick={() => {
              setShowMore(!showMore);
            }}
            className={clsx({
              "level-left": true,
              [styles["show-more"]]: true,
            })}
          >
            {" "}
            <p className="level-left level-item">Read More</p>
            <span className="level-left level-item icon is-small">
              <Arrow
                className={clsx({
                  [styles["arrow"]]: true,
                  [styles["rotate"]]: showMore,
                })}
              />
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
